import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

export default function HeroSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const subtitleRef = useRef(null)
  const madeInIndiaRef = useRef(null)

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const subtitle = subtitleRef.current
    const madeInIndia = madeInIndiaRef.current

    // Initial animation timeline
    const tl = gsap.timeline()
    
    tl.set([title, subtitle, madeInIndia], { opacity: 0, y: 50 })
      .to(title, { 
        opacity: 1, 
        y: 0, 
        duration: 1.5, 
        delay: 10,
        ease: 'power2.out' 
      })
      .to(subtitle, { 
        opacity: 1, 
        y: 0, 
        duration: 1, 
        ease: 'power2.out' 
      }, '-=0.5')
      .to(madeInIndia, { 
        opacity: 1, 
        y: 0, 
        duration: 1, 
        ease: 'power2.out' 
      }, '-=0.5')

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top top',
      end: 'bottom top',
      onUpdate: (self) => {
        const progress = self.progress
        
        // Fade out title as we scroll
        gsap.to(title, {
          opacity: 1 - progress * 0.8,
          scale: 1 - progress * 0.2,
          duration: 0.1
        })
        
        // Move subtitle up
        gsap.to(subtitle, {
          y: -progress * 100,
          opacity: 1 - progress * 0.5,
          duration: 0.1
        })
      }
    })

    // Hide loading screen after initial animations
    const loadingScreen = document.getElementById('loading-screen')
    if (loadingScreen) {
      gsap.to(loadingScreen, {
        opacity: 0,
        duration: 1,
        delay: 5,
        onComplete: () => {
          loadingScreen.style.display = 'none'
        }
      })
    }

    return () => {
      tl.kill()
    }
  }, [])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section relative"
      style={{ minHeight: '100vh' }}
    >
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="text-center z-20">
          {/* Main Title */}
          <h1 
            ref={titleRef}
            className="hero-title mb-8 text-shadow-glow-strong"
          >
            PRAVAHAN
          </h1>
          
          {/* Subtitle */}
          <p 
            ref={subtitleRef}
            className="subtitle max-w-2xl mx-auto text-white/85"
          >
            ENHANCING CONNECTIVITY THROUGH WATER
          </p>
          
          {/* Made in India Badge */}
          <div 
            ref={madeInIndiaRef}
            className="mt-8 inline-block px-6 py-3 glass-panel border border-primary/30"
          >
            <span className="font-rajdhani text-lg text-primary">
              Made in India
            </span>
          </div>
        </div>
      </div>

      {/* Floating particles effect */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        {[...Array(20)].map((_, i) => (
          <div
            key={i}
            className="absolute w-1 h-1 bg-primary rounded-full animate-pulse"
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
              animationDelay: `${Math.random() * 2}s`,
              animationDuration: `${2 + Math.random() * 2}s`
            }}
          />
        ))}
      </div>

      {/* Scroll indicator */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-primary rounded-full flex justify-center">
          <div className="w-1 h-3 bg-primary rounded-full mt-2 animate-pulse"></div>
        </div>
      </div>
    </section>
  )
} 