import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Rocket, Globe, Target, Award } from 'lucide-react'

export default function RoadmapSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const roadmapRef = useRef(null)

  const roadmap = [
    {
      year: '2024',
      phase: 'Phase 1',
      title: 'Commercial Launch',
      description: 'First commercial hydrofoil vessel deployment',
      milestones: ['Sea trials completed', 'Regulatory approvals', 'First customer contracts'],
      icon: Rocket,
      status: 'current'
    },
    {
      year: '2025',
      phase: 'Phase 2',
      title: 'Market Expansion',
      description: 'Regional expansion and fleet growth',
      milestones: ['5 vessels operational', 'New markets entry', 'Partnership expansion'],
      icon: Globe,
      status: 'upcoming'
    },
    {
      year: '2026',
      phase: 'Phase 3',
      title: 'Global Scale',
      description: 'International operations and technology licensing',
      milestones: ['20+ vessels worldwide', 'Technology licensing', 'International partnerships'],
      icon: Target,
      status: 'upcoming'
    },
    {
      year: '2027+',
      phase: 'Phase 4',
      title: 'Industry Leadership',
      description: 'Market leadership and innovation hub',
      milestones: ['Market leadership', 'R&D center', 'Global innovation hub'],
      icon: Award,
      status: 'future'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const roadmap = roadmapRef.current

    // Initial setup
    gsap.set([title, roadmap], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate roadmap with stagger
        gsap.to(roadmap, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.3,
          ease: 'power2.out'
        })

        // Animate roadmap items
        const items = document.querySelectorAll('.roadmap-item')
        gsap.fromTo(items, 
          { x: -100, opacity: 0 },
          { 
            x: 0, 
            opacity: 1, 
            duration: 0.8, 
            stagger: 0.3,
            ease: 'power2.out' 
          }
        )
      }
    })

    return () => {
      ScrollTrigger.getAll().forEach(trigger => {
        if (trigger.vars.trigger === section) {
          trigger.kill()
        }
      })
    }
  }, [])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            Future <span className="cyan-gradient">Roadmap</span>
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            Pravahan's strategic path to becoming the global leader in 
            hydrofoil maritime technology and transportation solutions.
          </p>
        </div>

        {/* Roadmap Timeline */}
        <div 
          ref={roadmapRef}
          className="relative mb-16"
        >
          {/* Timeline line */}
          <div className="absolute left-1/2 transform -translate-x-1/2 w-1 bg-white/20 h-full hidden lg:block"></div>
          
          <div className="space-y-12">
            {roadmap.map((phase, index) => {
              const IconComponent = phase.icon
              return (
                <div key={index} className="roadmap-item relative">
                  {/* Timeline dot */}
                  <div className={`absolute left-1/2 transform -translate-x-1/2 w-6 h-6 rounded-full border-4 hidden lg:block ${
                    phase.status === 'current' 
                      ? 'bg-primary border-primary shadow-[0_0_20px_rgba(0,255,255,0.5)]' 
                      : phase.status === 'upcoming'
                      ? 'bg-yellow-400 border-yellow-400'
                      : 'bg-white/20 border-white/40'
                  }`}></div>
                  
                  {/* Content */}
                  <div className={`lg:w-5/12 ${index % 2 === 0 ? 'lg:pr-8 lg:text-right' : 'lg:pl-8 lg:ml-auto'}`}>
                    <div className={`glass-panel p-6 relative ${
                      phase.status === 'current' ? 'border border-primary/30 shadow-[0_0_40px_rgba(0,255,255,0.1)]' : ''
                    }`}>
                      {/* Phase indicator */}
                      <div className={`inline-block px-3 py-1 rounded-full text-xs font-bold mb-3 ${
                        phase.status === 'current' 
                          ? 'bg-primary text-white' 
                          : phase.status === 'upcoming'
                          ? 'bg-yellow-400 text-black'
                          : 'bg-white/20 text-white/70'
                      }`}>
                        {phase.phase}
                      </div>
                      
                      {/* Year */}
                      <div className="text-sm text-primary font-bold mb-2">
                        {phase.year}
                      </div>
                      
                      {/* Icon */}
                      <div className={`w-12 h-12 rounded-full bg-gradient-to-br ${
                        phase.status === 'current' 
                          ? 'from-primary to-secondary' 
                          : 'from-white/20 to-white/10'
                      } flex items-center justify-center mb-4 ${index % 2 === 0 ? 'lg:ml-auto' : ''}`}>
                        <IconComponent className={`w-6 h-6 ${
                          phase.status === 'current' ? 'text-white' : 'text-white/70'
                        }`} />
                      </div>
                      
                      {/* Title */}
                      <h3 className={`text-xl font-rajdhani font-bold mb-3 ${
                        phase.status === 'current' ? 'text-primary' : 'text-white'
                      }`}>
                        {phase.title}
                      </h3>
                      
                      {/* Description */}
                      <p className="text-white/70 mb-4 leading-relaxed">
                        {phase.description}
                      </p>
                      
                      {/* Milestones */}
                      <ul className="space-y-2">
                        {phase.milestones.map((milestone, mIndex) => (
                          <li key={mIndex} className="flex items-start text-sm">
                            <div className={`w-2 h-2 rounded-full mt-2 mr-3 flex-shrink-0 ${
                              phase.status === 'current' ? 'bg-primary' : 'bg-white/40'
                            }`}></div>
                            <span className="text-white/70">{milestone}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              )
            })}
          </div>
        </div>

        {/* Vision Statement */}
        <div className="glass-panel p-8 text-center">
          <div className="max-w-4xl mx-auto">
            <h3 className="text-2xl font-rajdhani font-bold text-white mb-6">
              Our Vision
            </h3>
            <p className="text-lg text-white/80 leading-relaxed mb-6">
              To revolutionize maritime transportation by making hydrofoil technology 
              the standard for speed, efficiency, and sustainability across all 
              maritime operations worldwide.
            </p>
            <div className="flex items-center justify-center space-x-8">
              <div className="text-center">
                <div className="text-3xl font-bold text-primary">100+</div>
                <div className="text-white/70">Vessels by 2030</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-primary">50+</div>
                <div className="text-white/70">Countries</div>
              </div>
              <div className="text-center">
                <div className="text-3xl font-bold text-primary">1M+</div>
                <div className="text-white/70">Passengers annually</div>
              </div>
            </div>
          </div>
        </div>

        {/* Call to Action */}
        <div className="text-center mt-16">
          <div className="glass-panel inline-block px-8 py-6">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <Rocket className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-rajdhani font-bold text-white">
                  Join the Revolution
                </h3>
                <p className="text-white/70">
                  Be part of the future of maritime transportation
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
} 