import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { TrendingUp, Shield, Zap, Target } from 'lucide-react'

export default function TechnologySection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const advantagesRef = useRef(null)
  const comparisonRef = useRef(null)

  const advantages = [
    {
      icon: TrendingUp,
      title: 'Performance',
      metric: '3X Speed',
      description: 'Unmatched velocity compared to traditional vessels',
      color: 'from-green-400 to-emerald-500'
    },
    {
      icon: Shield,
      title: 'Safety',
      metric: '99.9%',
      description: 'Reliability rate with advanced stability systems',
      color: 'from-blue-400 to-cyan-500'
    },
    {
      icon: Zap,
      title: 'Efficiency',
      metric: '70% Less',
      description: 'Fuel consumption reduction through hydrofoil design',
      color: 'from-yellow-400 to-orange-500'
    },
    {
      icon: Target,
      title: 'Precision',
      metric: '0.1',
      description: 'Navigation accuracy with advanced control systems',
      color: 'from-purple-400 to-pink-500'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const advantages = advantagesRef.current
    const comparison = comparisonRef.current

    // Initial setup
    gsap.set([title, advantages, comparison], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate advantages with stagger
        gsap.to(advantages, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.3,
          ease: 'power2.out'
        })

        // Animate comparison
        gsap.to(comparison, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.6,
          ease: 'power2.out'
        })
      }
    })

    // Animate metrics on scroll
    const metrics = document.querySelectorAll('.metric-value')
    metrics.forEach((metric, index) => {
      ScrollTrigger.create({
        trigger: metric,
        start: 'top 90%',
        onEnter: () => {
          gsap.fromTo(metric, 
            { scale: 0, opacity: 0 },
            { 
              scale: 1, 
              opacity: 1, 
              duration: 0.8, 
              delay: index * 0.1,
              ease: 'back.out(1.7)' 
            }
          )
        }
      })
    })

    return () => {
      ScrollTrigger.getAll().forEach(trigger => {
        if (trigger.vars.trigger === section) {
          trigger.kill()
        }
      })
    }
  }, [])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            <span className="cyan-gradient">Technology</span> Advantages
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            Advanced hydrofoil technology delivers unprecedented performance 
            across all operational parameters.
          </p>
        </div>

        {/* Technology Advantages Grid */}
        <div 
          ref={advantagesRef}
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-16"
        >
          {advantages.map((advantage, index) => {
            const IconComponent = advantage.icon
            return (
              <div
                key={index}
                className="feature-card group cursor-pointer"
              >
                {/* Icon */}
                <div className={`w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br ${advantage.color} flex items-center justify-center group-hover:scale-110 transition-transform duration-300`}>
                  <IconComponent className="w-8 h-8 text-white" />
                </div>

                {/* Title */}
                <h3 className="text-xl font-rajdhani font-bold mb-4 text-white">
                  {advantage.title}
                </h3>

                {/* Metric */}
                <div className="metric-value text-3xl font-bold text-primary mb-2">
                  {advantage.metric}
                </div>

                {/* Description */}
                <p className="text-white/70 text-sm leading-relaxed">
                  {advantage.description}
                </p>

                {/* Hover effect */}
                <div className="absolute inset-0 bg-gradient-to-br from-primary/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-2xl -z-10"></div>
              </div>
            )
          })}
        </div>

        {/* Technology Comparison */}
        <div 
          ref={comparisonRef}
          className="glass-panel p-8 mb-16"
        >
          <h3 className="text-2xl font-rajdhani font-bold text-white mb-8 text-center">
            Performance Comparison
          </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {/* Conventional Vessels */}
            <div className="text-center">
              <h4 className="text-lg font-rajdhani font-bold text-white/70 mb-4">
                Conventional Vessels
              </h4>
              <div className="space-y-3">
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">20 knots</div>
                  <div className="text-sm text-white/40">Max Speed</div>
                </div>
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">High</div>
                  <div className="text-sm text-white/40">Fuel Consumption</div>
                </div>
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">Limited</div>
                  <div className="text-sm text-white/40">Maneuverability</div>
                </div>
              </div>
            </div>

            {/* Pravahan Hydrofoil */}
            <div className="text-center relative">
              <div className="absolute -top-4 left-1/2 transform -translate-x-1/2">
                <div className="bg-gradient-to-r from-primary to-secondary text-white px-4 py-2 rounded-full text-sm font-bold">
                  PRAVAHAN
                </div>
              </div>
              <h4 className="text-lg font-rajdhani font-bold text-primary mb-4 mt-4">
                Hydrofoil Technology
              </h4>
              <div className="space-y-3">
                <div className="p-3 bg-primary/10 border border-primary/30 rounded-lg">
                  <div className="text-2xl font-bold text-primary">60 knots</div>
                  <div className="text-sm text-primary/70">Max Speed</div>
                </div>
                <div className="p-3 bg-primary/10 border border-primary/30 rounded-lg">
                  <div className="text-2xl font-bold text-primary">70% Less</div>
                  <div className="text-sm text-primary/70">Fuel Consumption</div>
                </div>
                <div className="p-3 bg-primary/10 border border-primary/30 rounded-lg">
                  <div className="text-2xl font-bold text-primary">Superior</div>
                  <div className="text-sm text-primary/70">Maneuverability</div>
                </div>
              </div>
            </div>

            {/* Air Transport */}
            <div className="text-center">
              <h4 className="text-lg font-rajdhani font-bold text-white/70 mb-4">
                Air Transport
              </h4>
              <div className="space-y-3">
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">500+ knots</div>
                  <div className="text-sm text-white/40">Max Speed</div>
                </div>
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">Very High</div>
                  <div className="text-sm text-white/40">Fuel Consumption</div>
                </div>
                <div className="p-3 bg-white/5 rounded-lg">
                  <div className="text-2xl font-bold text-white/50">Limited</div>
                  <div className="text-sm text-white/40">Payload Capacity</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Technology Highlight */}
        <div className="text-center">
          <div className="glass-panel inline-block px-8 py-6">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <Zap className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-rajdhani font-bold text-white">
                  Best of Both Worlds
                </h3>
                <p className="text-white/70">
                  Speed of air transport with the payload capacity of maritime vessels
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
} 