import React, { useRef, useEffect, useMemo, useState } from 'react'
import { Canvas, useFrame, useThree } from '@react-three/fiber'
import { Sky, OrbitControls, useGLTF, Environment } from '@react-three/drei'
import * as THREE from 'three'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

gsap.registerPlugin(ScrollTrigger)

// Advanced Water Shader from threejs-water-shader project
const waterVertexShader = `
precision highp float;

uniform float uTime;

uniform float uWavesAmplitude;
uniform float uWavesSpeed;
uniform float uWavesFrequency;
uniform float uWavesPersistence;
uniform float uWavesLacunarity;
uniform float uWavesIterations;

varying vec3 vNormal;
varying vec3 vWorldPosition;

//	Simplex 3D Noise 
//	by Ian McEwan, Stefan Gustavson (https://github.com/stegu/webgl-noise)
//
vec4 permute(vec4 x) {
  return mod(((x * 34.0) + 1.0) * x, 289.0);
}
vec4 taylorInvSqrt(vec4 r) {
  return 1.79284291400159 - 0.85373472095314 * r;
}

// Simplex 2D noise
//
vec3 permute(vec3 x) {
  return mod(((x * 34.0) + 1.0) * x, 289.0);
}

float snoise(vec2 v) {
  const vec4 C = vec4(0.211324865405187, 0.366025403784439, -0.577350269189626, 0.024390243902439);
  vec2 i = floor(v + dot(v, C.yy));
  vec2 x0 = v - i + dot(i, C.xx);
  vec2 i1;
  i1 = (x0.x > x0.y) ? vec2(1.0, 0.0) : vec2(0.0, 1.0);
  vec4 x12 = x0.xyxy + C.xxzz;
  x12.xy -= i1;
  i = mod(i, 289.0);
  vec3 p = permute(permute(i.y + vec3(0.0, i1.y, 1.0)) + i.x + vec3(0.0, i1.x, 1.0));
  vec3 m = max(0.5 - vec3(dot(x0, x0), dot(x12.xy, x12.xy), dot(x12.zw, x12.zw)), 0.0);
  m = m * m;
  m = m * m;
  vec3 x = 2.0 * fract(p * C.www) - 1.0;
  vec3 h = abs(x) - 0.5;
  vec3 ox = floor(x + 0.5);
  vec3 a0 = x - ox;
  m *= 1.79284291400159 - 0.85373472095314 * (a0 * a0 + h * h);
  vec3 g;
  g.x = a0.x * x0.x + h.x * x0.y;
  g.yz = a0.yz * x12.xz + h.yz * x12.yw;
  return 130.0 * dot(m, g);
}

// Helper function to calculate elevation at any point
float getElevation(float x, float z) {
  vec2 pos = vec2(x, z);

  float elevation = 0.0;
  float amplitude = 1.0;
  float frequency = uWavesFrequency;
  vec2 p = pos.xy;

  for(float i = 0.0; i < uWavesIterations; i++) {
    float noiseValue = snoise(p * frequency + uTime * uWavesSpeed);
    elevation += amplitude * noiseValue;
    amplitude *= uWavesPersistence;
    frequency *= uWavesLacunarity;
  }

  elevation *= uWavesAmplitude;

  return elevation;
}

void main() {
  vec4 modelPosition = modelMatrix * vec4(position, 1.0);

  float elevation = getElevation(modelPosition.x, modelPosition.z);
  modelPosition.y += elevation;

  // Calculate normal using partial derivatives
  float eps = 0.001;
  vec3 tangent = normalize(vec3(eps, getElevation(modelPosition.x - eps, modelPosition.z) - elevation, 0.0));
  vec3 bitangent = normalize(vec3(0.0, getElevation(modelPosition.x, modelPosition.z - eps) - elevation, eps));
  vec3 objectNormal = normalize(cross(tangent, bitangent));

  vNormal = objectNormal;
  vWorldPosition = modelPosition.xyz;

  gl_Position = projectionMatrix * viewMatrix * modelPosition;
}
`

const waterFragmentShader = `
precision highp float;

uniform float uOpacity;

uniform vec3 uTroughColor;
uniform vec3 uSurfaceColor;
uniform vec3 uPeakColor;

uniform float uPeakThreshold;
uniform float uPeakTransition;
uniform float uTroughThreshold;
uniform float uTroughTransition;

uniform float uFresnelScale;
uniform float uFresnelPower;

varying vec3 vNormal;
varying vec3 vWorldPosition;

uniform samplerCube uEnvironmentMap;

void main() {
  // Calculate vector from camera to the vertex
  vec3 viewDirection = normalize(vWorldPosition - cameraPosition);
  vec3 reflectedDirection = reflect(viewDirection, vNormal);
  reflectedDirection.x = -reflectedDirection.x;

  // Sample environment map to get the reflected color
  vec4 reflectionColor = textureCube(uEnvironmentMap, reflectedDirection);

  // Calculate fresnel effect
  float fresnel = uFresnelScale * pow(1.0 - clamp(dot(viewDirection, vNormal), 0.0, 1.0), uFresnelPower);

  // Calculate elevation-based color
  float elevation = vWorldPosition.y;

  // Calculate transition factors using smoothstep
  float peakFactor = smoothstep(uPeakThreshold - uPeakTransition, uPeakThreshold + uPeakTransition, elevation);
  float troughFactor = smoothstep(uTroughThreshold - uTroughTransition, uTroughThreshold + uTroughTransition, elevation);

  // Mix between trough and surface colors based on trough transition
  vec3 mixedColor1 = mix(uTroughColor, uSurfaceColor, troughFactor);

  // Mix between surface and peak colors based on peak transition 
  vec3 mixedColor2 = mix(mixedColor1, uPeakColor, peakFactor);

  // Mix the final color with the reflection color
  vec3 finalColor = mix(mixedColor2, reflectionColor.rgb, fresnel);

  gl_FragColor = vec4(finalColor, uOpacity);
}
`

const WATER_PHASES = {
  INTRO: 'intro',
  FOIL: 'foil',
  ANIMATE: 'animate',
  TITLE: 'title',
  SCROLL: 'scroll',
}

// Improved Water with simple sine wave effect
function SimpleWater() {
  const meshRef = useRef()
  const radius = 400
  useFrame(({ clock }) => {
    if (meshRef.current) {
      const time = clock.getElapsedTime()
      meshRef.current.rotation.y = time * 0.03 // slow rotation for movement feel
      const position = meshRef.current.geometry.attributes.position
      for (let i = 0; i < position.count; i++) {
        // Get spherical coordinates
        const x = position.getX(i)
        const y = position.getY(i)
        const z = position.getZ(i)
        // Only apply waves near the 'top' (y ~ radius)
        const distFromTop = Math.abs(y - radius)
        let wave = 0
        if (distFromTop < 40) {
          wave = Math.sin(x * 0.08 + time * 0.7) * 0.5 + Math.cos(z * 0.08 + time * 0.5) * 0.5
        }
        position.setY(i, y + wave)
      }
      position.needsUpdate = true
      meshRef.current.geometry.computeVertexNormals()
    }
  })
  return (
    <mesh ref={meshRef} position={[0, 0, 0]}>
      <sphereGeometry args={[radius, 128, 64, 0, Math.PI * 2, 0, Math.PI / 2]} />
      <meshPhongMaterial color="#186691" transparent opacity={0.5} shininess={120} side={THREE.DoubleSide} />
    </mesh>
  )
}

// Advanced Water Component
function AdvancedWater() {
  const meshRef = useRef()
  const materialRef = useRef()

  // Create environment map for water reflections using actual textures
  const environmentMap = useMemo(() => {
    try {
      const cubeTextureLoader = new THREE.CubeTextureLoader()
      cubeTextureLoader.setPath('/')
      return cubeTextureLoader.load([
        'px.png', // positive x
        'nx.png', // negative x 
        'py.png', // positive y
        'ny.png', // negative y
        'pz.png', // positive z
        'nz.png'  // negative z
      ])
    } catch (error) {
      console.log('Environment map not found, using fallback')
      // Create a simple fallback environment map
      const canvas = document.createElement('canvas')
      canvas.width = 1
      canvas.height = 1
      const ctx = canvas.getContext('2d')
      ctx.fillStyle = '#87CEEB' // Sky blue
      ctx.fillRect(0, 0, 1, 1)
      const texture = new THREE.CanvasTexture(canvas)
      return texture
    }
  }, [])

  useEffect(() => {
    if (materialRef.current) {
      try {
        materialRef.current.uniforms.uEnvironmentMap.value = environmentMap
      } catch (error) {
        console.log('Shader error, using fallback water')
      }
    }
  }, [environmentMap])

  useFrame(({ clock }) => {
    if (materialRef.current) {
      try {
        materialRef.current.uniforms.uTime.value = clock.getElapsedTime()
      } catch (error) {
        console.log('Shader error, using fallback water')
      }
    }
  })

  // If shader fails, use simple water
  if (!environmentMap) {
    return <SimpleWater />
  }

  return (
    <mesh ref={meshRef} rotation={[-Math.PI / 2, 0, 0]} position={[0, 0, 0]}>
      <planeGeometry args={[2000, 2000, 1024, 1024]} />
      <shaderMaterial
        ref={materialRef}
        vertexShader={waterVertexShader}
        fragmentShader={waterFragmentShader}
        uniforms={{
          uTime: { value: 0 },
          uOpacity: { value: 0.9 },
          uEnvironmentMap: { value: environmentMap },
          uWavesAmplitude: { value: 0.008 },
          uWavesFrequency: { value: 1.0 },
          uWavesPersistence: { value: 0.3 },
          uWavesLacunarity: { value: 2.18 },
          uWavesIterations: { value: 10 },
          uWavesSpeed: { value: 0.4 },
          uTroughColor: { value: new THREE.Color('#186691') },
          uSurfaceColor: { value: new THREE.Color('#9bd8c0') },
          uPeakColor: { value: new THREE.Color('#bbd8e0') },
          uPeakThreshold: { value: 0.08 },
          uPeakTransition: { value: 0.05 },
          uTroughThreshold: { value: -0.01 },
          uTroughTransition: { value: 0.15 },
          uFresnelScale: { value: 0.8 },
          uFresnelPower: { value: 0.5 }
        }}
        transparent={true}
        depthTest={true}
        side={THREE.DoubleSide}
      />
    </mesh>
  )
}

// Pravahan Vessel Component using actual GLTF
function PravahanVessel({ vesselRef, phase, vesselState }) {
  const { scene } = useGLTF('/assembly_2_pravahan/scene.gltf')

  useEffect(() => {
    if (vesselRef.current) {
      vesselRef.current.scale.set(10, 10, 10)
      vesselRef.current.rotation.y = Math.PI / 4
    }
  }, [vesselRef])

  useFrame(() => {
    if (vesselRef.current) {
      // Set vessel position and rotation from vesselState
      vesselRef.current.position.set(
        vesselState.x,
        vesselState.y,
        vesselState.z
      )
      vesselRef.current.rotation.set(
        vesselState.rx,
        vesselState.ry,
        vesselState.rz
      )
    }
  })

  return <primitive ref={vesselRef} object={scene} />
}

// Camera controller with end turn and scroll orbit
function CameraController({ vesselRef, phase, vesselState }) {
  const { camera } = useThree()
  const [endTurn, setEndTurn] = useState(false)
  const endTurnRef = useRef(false)

  // Start camera turn at the end of animation
  useEffect(() => {
    if (phase === 'title') {
      setEndTurn(true)
      endTurnRef.current = true
    } else if (phase === 'scroll') {
      setEndTurn(false)
      endTurnRef.current = false
    }
  }, [phase])

  // Track camera turn angle
  const turnAngle = useRef(0)

  useFrame(() => {
    if (!vesselRef.current) return
    const v = vesselState
    if (phase === 'scroll') {
      // Camera orbits vessel on scroll
      const scrollY = window.scrollY / (document.body.scrollHeight - window.innerHeight)
      const angle = scrollY * 2 * Math.PI
      const radius = 18
      const camX = v.x + radius * Math.cos(angle)
      const camZ = v.z + radius * Math.sin(angle)
      const camY = v.y + 7 + Math.sin(angle) * 2
      camera.position.lerp(new THREE.Vector3(camX, camY, camZ), 0.008)
      camera.lookAt(v.x, v.y, v.z)
    } 
    else if (endTurnRef.current) {
      // At end, smoothly rotate camera around vessel
      turnAngle.current += 0.01
      const radius = 30
      const camX = v.x + radius * Math.cos(turnAngle.current)
      const camZ = v.z + radius * Math.sin(turnAngle.current)
      const camY = v.y + 7 + Math.sin(turnAngle.current) * 2
      camera.position.lerp(new THREE.Vector3(camX, camY, camZ), 0.004)
      camera.lookAt(v.x, v.y, v.z)
    } else {
      // Animation: follow vessel from behind/above at 60deg
      const angle = Math.PI
      const camDist = 25
      const camHeight = 4
      const camX = v.x - Math.cos(angle) * camDist
      const camZ = v.z - Math.sin(angle) * camDist
      const camY = camHeight
      camera.position.lerp(new THREE.Vector3(camX, camY, camZ), 0.0095)
      camera.lookAt(v.x, v.y, v.z)
    }
})
  return null
}

// Ocean foam particles
function OceanFoamParticles({ count = 500 }) {
  const particles = useMemo(() => {
    const temp = []
    for (let i = 0; i < count; i++) {
      const time = Math.random() * 100
      const speed = 0.1 + Math.random() * 0.1
      const size = Math.random() * 0.5
      const x = (Math.random() - 0.5) * 100
      const z = (Math.random() - 0.5) * 100
      temp.push({ time, speed, size, x, z })
    }
    return temp
  }, [count])

  const points = useMemo(() => {
    const positions = new Float32Array(particles.length * 3)
    particles.forEach((particle, i) => {
      positions[i * 3] = particle.x
      positions[i * 3 + 1] = 0
      positions[i * 3 + 2] = particle.z
    })
    return positions
  }, [particles])

  useFrame(({ clock }) => {
    particles.forEach((particle, i) => {
      particle.time += particle.speed
      const y = Math.sin(particle.time) * 0.5
      // Update particle positions here if needed
    })
  })

  return (
    <points>
      <bufferGeometry>
        <bufferAttribute
          attach="attributes-position"
          count={particles.length}
          array={points}
          itemSize={3}
        />
      </bufferGeometry>
      <pointsMaterial
        size={0.1}
        color="#ffffff"
        transparent
        opacity={0.6}
        sizeAttenuation
      />
    </points>
  )
}

function TitleOverlay({ show }) {
  return (
    <div
      className={`fixed inset-0 flex items-center justify-center z-50 pointer-events-none transition-opacity duration-1000 ${show ? 'opacity-100' : 'opacity-0'}`}
      style={{ fontFamily: 'inherit' }}
    >
    </div>
  )
}

// Main Ocean Scene
function OceanSceneContent({ phase, vesselState }) {
  const vesselRef = useRef()
  const { scene } = useThree()

  // Create environment map for scene background
  const environmentMap = useMemo(() => {
    const cubeTextureLoader = new THREE.CubeTextureLoader()
    cubeTextureLoader.setPath('/')
    return cubeTextureLoader.load([
      'px.png', 'nx.png', 'py.png', 'ny.png', 'pz.png', 'nz.png'
    ])
  }, [])

  // Set environment map as background and environment
  useEffect(() => {
    if (environmentMap) {
      scene.background = environmentMap
      scene.environment = environmentMap
    }
  }, [environmentMap, scene])

  // Always render vessel and water, regardless of phase
  return (
    <>
      {/* Lighting */}
      <ambientLight intensity={0.01} color="#001133" />
      <directionalLight
        position={[100, 100, 50]}
        intensity={0.4}
        color="#ffffff"
        castShadow
      />
      {/* Water - always rendered */}
      <AdvancedWater />
      {/* Vessel - always rendered */}
      <PravahanVessel vesselRef={vesselRef} phase={phase} vesselState={vesselState} />
      {/* Foam Particles */}
      <OceanFoamParticles count={500} />
      {/* Camera Controller */}
      <CameraController vesselRef={vesselRef} phase={phase} vesselState={vesselState} />
    </>
  )
}

export default function OceanScene() {
  const vesselRef = useRef()
  const [phase, setPhase] = useState(WATER_PHASES.INTRO)
  const [vesselState, setVesselState] = useState({
    x: 0, y: 2, z: 0, rx: 0, ry: Math.PI / 4, rz: 0
  })

  // Animation timeline
  useEffect(() => {
    // GSAP timeline for constant speed
    const tl = gsap.timeline()
    // 1. Move diagonally for 10s (y=1)
    tl.to(vesselState, {
      x: 100, z: 100, duration: 5,
      ease: 'linear',
      onUpdate: () => setVesselState({ ...vesselState })
    })
    // 2. Foil and continue moving for 5s
    tl.to(vesselState, {
      y: 6, x: 150, z: 150, duration: 2.5,
      ease: 'linear', // keep constant speed for x/z
      onUpdate: () => setVesselState({ ...vesselState }),
      onStart: () => setPhase(WATER_PHASES.FOIL)
    }, '>-0.5')
    // 3. Continue for 25s with roll/pitch
    tl.to(vesselState, {
      x: 800, z: 800, duration: 20,
      ease: 'linear',
      onUpdate: () => setVesselState({ ...vesselState }),
      // onStart: () => setPhase(WATER_PHASES.ANIMATE),
      onComplete: () => setPhase(WATER_PHASES.TITLE)
    })
    tl.to(vesselState, {
      y:2, x: 900, z: 900, duration: 3.5,
      ease: 'linear',
      onUpdate: () => setVesselState({ ...vesselState }),
      onStart: () => setPhase(WATER_PHASES.ANIMATE),
      onComplete: () => setPhase(WATER_PHASES.TITLE)
    })
    // 4. Hold for 2s, then scroll phase
    tl.to({}, { duration: 2, onComplete: () => setPhase(WATER_PHASES.SCROLL) })
    // eslint-disable-next-line
  }, [])

  // In OceanScene, update roll/pitch oscillation to be present from the start
  useEffect(() => {
    let frameId
    function animateOscillation() {
      setVesselState((prev) => ({
        ...prev,
        rx: Math.sin(Date.now() * 0.002) * 0.04, // pitch
        rz: Math.sin(Date.now() * 0.001) * 0.035, // roll
      }))
      frameId = requestAnimationFrame(animateOscillation)
    }
    animateOscillation()
    return () => frameId && cancelAnimationFrame(frameId)
  }, [])

  return (
    <div className="relative w-full h-screen">
      <Canvas
        camera={{ position: [10, 10, 5], fov: 65 }}
        gl={{ 
          antialias: true,
          alpha: true,
          powerPreference: "high-performance"
        }}
        style={{ width: '100vw', height: '100vh', background: 'transparent' }}
      >
        <OceanSceneContent phase={phase} vesselState={vesselState} />
      </Canvas>
      {/* Title overlay */}
      <TitleOverlay show={phase === WATER_PHASES.TITLE} />
    </div>
  )
} 