import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Ship, Users, Shield } from 'lucide-react'

export default function ProblemSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const cardsRef = useRef([])

  const problems = [
    {
      icon: Ship,
      title: 'Cargo Transport',
      description: 'Slow maritime logistics with 15-20 days transit times',
      stats: '40% slower than air freight',
      color: 'from-blue-500 to-cyan-500'
    },
    {
      icon: Users,
      title: 'Passenger Travel',
      description: 'Limited high-speed maritime passenger options',
      stats: 'Only 2% of global travel',
      color: 'from-purple-500 to-pink-500'
    },
    {
      icon: Shield,
      title: 'Defense Operations',
      description: 'Vulnerable slow-moving naval assets',
      stats: '60% of naval losses due to speed',
      color: 'from-red-500 to-orange-500'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const cards = cardsRef.current

    // Initial setup
    gsap.set([title, ...cards], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate cards with stagger
        gsap.to(cards, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          stagger: 0.2,
          ease: 'power2.out'
        })
      },
      onLeave: () => {
        gsap.to([title, ...cards], {
          opacity: 0,
          y: -50,
          duration: 0.5
        })
      },
      onEnterBack: () => {
        gsap.to([title, ...cards], {
          opacity: 1,
          y: 0,
          duration: 0.8
        })
      }
    })

    // Parallax effect for cards
    cards.forEach((card, index) => {
      ScrollTrigger.create({
        trigger: card,
        start: 'top bottom',
        end: 'bottom top',
        onUpdate: (self) => {
          const progress = self.progress
          gsap.to(card, {
            y: progress * -20,
            rotationY: progress * 5,
            duration: 0.1
          })
        }
      })
    })

    return () => {
      ScrollTrigger.getAll().forEach(trigger => {
        if (trigger.vars.trigger === section || trigger.vars.trigger === cards[0]) {
          trigger.kill()
        }
      })
    }
  }, [])

  const addToCardsRef = (el) => {
    if (el && !cardsRef.current.includes(el)) {
      cardsRef.current.push(el)
    }
  }

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            The <span className="cyan-gradient">Challenge</span>
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            Traditional maritime transportation faces critical limitations in speed, 
            efficiency, and operational capabilities across three key sectors.
          </p>
        </div>

        {/* Problem Cards Grid */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {problems.map((problem, index) => {
            const IconComponent = problem.icon
            return (
              <div
                key={index}
                ref={addToCardsRef}
                className="feature-card group cursor-pointer"
                style={{ transformStyle: 'preserve-3d' }}
              >
                {/* Icon */}
                <div className={`w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br ${problem.color} flex items-center justify-center group-hover:scale-110 transition-transform duration-300`}>
                  <IconComponent className="w-8 h-8 text-white" />
                </div>

                {/* Content */}
                <h3 className="text-2xl font-rajdhani font-bold mb-4 text-white">
                  {problem.title}
                </h3>
                
                <p className="text-white/70 mb-4 leading-relaxed">
                  {problem.description}
                </p>

                {/* Stats */}
                <div className="mt-6 p-4 bg-white/5 rounded-lg border border-white/10">
                  <div className="text-2xl font-bold text-primary mb-1">
                    {problem.stats}
                  </div>
                  <div className="text-sm text-white/50">
                    Current limitation
                  </div>
                </div>

                {/* Hover effect */}
                <div className="absolute inset-0 bg-gradient-to-br from-primary/10 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300 rounded-2xl -z-10"></div>
              </div>
            )
          })}
        </div>

        {/* Bottom CTA */}
        <div className="text-center mt-16">
          <div className="glass-panel inline-block px-8 py-4">
            <p className="text-lg text-white/80">
              But what if we could change everything?
            </p>
          </div>
        </div>
      </div>
    </section>
  )
} 