import React, { useEffect, useRef, useState } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import OceanScene from './components/OceanScene'
import HeroSection from './components/HeroSection'
import ProblemSection from './components/ProblemSection'
import SolutionSection from './components/SolutionSection'
import TechnologySection from './components/TechnologySection'
import AdvantagesSection from './components/AdvantagesSection'
import ProgressSection from './components/ProgressSection'
import MarketSection from './components/MarketSection'
import RoadmapSection from './components/RoadmapSection'

// Register GSAP plugins
gsap.registerPlugin(ScrollTrigger)

function App() {
  const containerRef = useRef(null)
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    // Initialize GSAP ScrollTrigger
    ScrollTrigger.config({
      ignoreMobileResize: true,
    })

    // Hide loading after 1 second
    const timer = setTimeout(() => setLoading(false), 1000)

    // Cleanup function
    return () => {
      clearTimeout(timer)
      ScrollTrigger.getAll().forEach(trigger => trigger.kill())
    }
  }, [])

  return (
    <div ref={containerRef} className="relative">
      {/* Fixed 3D Scene */}
      <div className="fixed inset-0 z-0">
        <OceanScene />
      </div>

      {/* Scrollable Content */}
      <div className="relative z-10">
        <HeroSection />
        <ProblemSection />
        <SolutionSection />
        <TechnologySection />
        <AdvantagesSection />
        <ProgressSection />
        <MarketSection />
        <RoadmapSection />
      </div>

      {/* Loading Screen */}
      <div id="loading-screen" className={`fixed inset-0 z-50 bg-dark flex items-center justify-center transition-opacity duration-700 ${loading ? 'opacity-100 pointer-events-auto' : 'opacity-0 pointer-events-none'}`}>
        <div className="text-center">
          <div className="hero-title mb-4">Loading...</div>
          <div className="subtitle">India's First Hydrofoil boat</div>
          <div className="mt-8 w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto"></div>
        </div>
      </div>
    </div>
  )
}

export default App 