import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Globe, TrendingUp, DollarSign, Users } from 'lucide-react'

export default function MarketSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const chartRef = useRef(null)

  const marketData = [
    {
      sector: 'Cargo Transport',
      size: 42,
      growth: 8.5,
      color: 'from-blue-400 to-cyan-500'
    },
    {
      sector: 'Passenger Travel',
      size: 28,
      growth: 12.3,
      color: 'from-purple-400 to-pink-500'
    },
    {
      sector: 'Defense & Security',
      size: 35,
      growth: 6.8,
      color: 'from-red-400 to-orange-500'
    },
    {
      sector: 'Tourism & Leisure',
      size: 18,
      growth: 15.2,
      color: 'from-green-400 to-emerald-500'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const chart = chartRef.current

    // Initial setup
    gsap.set([title, chart], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate chart with stagger
        gsap.to(chart, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.3,
          ease: 'power2.out'
        })

        // Animate bubbles
        const bubbles = document.querySelectorAll('.market-bubble')
        gsap.fromTo(bubbles, 
          { scale: 0, opacity: 0 },
          { 
            scale: 1, 
            opacity: 1, 
            duration: 0.8, 
            stagger: 0.2,
            ease: 'back.out(1.7)' 
          }
        )
      }
    })

    return () => {
      ScrollTrigger.getAll().forEach(trigger => {
        if (trigger.vars.trigger === section) {
          trigger.kill()
        }
      })
    }
  }, [])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            Market <span className="cyan-gradient">Opportunity</span>
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            A $42 billion global maritime market ripe for disruption with 
            hydrofoil technology addressing critical industry needs.
          </p>
        </div>

        {/* Market Overview */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-16">
          <div className="feature-card text-center">
            <Globe className="w-12 h-12 text-primary mx-auto mb-4" />
            <div className="text-3xl font-bold text-primary mb-2">
              $42B
            </div>
            <div className="text-white/70">Global Market Size</div>
          </div>
          
          <div className="feature-card text-center">
            <TrendingUp className="w-12 h-12 text-primary mx-auto mb-4" />
            <div className="text-3xl font-bold text-primary mb-2">
              10.7%
            </div>
            <div className="text-white/70">Annual Growth Rate</div>
          </div>
          
          <div className="feature-card text-center">
            <DollarSign className="w-12 h-12 text-primary mx-auto mb-4" />
            <div className="text-3xl font-bold text-primary mb-2">
              $5.2B
            </div>
            <div className="text-white/70">Addressable Market</div>
          </div>
          
          <div className="feature-card text-center">
            <Users className="w-12 h-12 text-primary mx-auto mb-4" />
            <div className="text-3xl font-bold text-primary mb-2">
              2.3B
            </div>
            <div className="text-white/70">Potential Users</div>
          </div>
        </div>

        {/* Market Bubble Chart */}
        <div 
          ref={chartRef}
          className="glass-panel p-8 mb-16"
        >
          <h3 className="text-2xl font-rajdhani font-bold text-white mb-8 text-center">
            Market Segmentation
          </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {marketData.map((market, index) => (
              <div key={index} className="text-center">
                <div className={`market-bubble w-32 h-32 mx-auto mb-4 rounded-full bg-gradient-to-br ${market.color} flex items-center justify-center text-white font-bold text-lg`}>
                  ${market.size}B
                </div>
                <h4 className="text-lg font-rajdhani font-bold text-white mb-2">
                  {market.sector}
                </h4>
                <div className="text-primary font-bold">
                  {market.growth}% CAGR
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Market Drivers */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-16">
          <div className="glass-panel p-6">
            <h3 className="text-xl font-rajdhani font-bold text-white mb-4">
              Market Drivers
            </h3>
            <ul className="space-y-3 text-white/70">
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Increasing demand for faster maritime transport</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Environmental regulations driving efficiency</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Growing e-commerce and logistics needs</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Defense modernization programs</span>
              </li>
            </ul>
          </div>

          <div className="glass-panel p-6">
            <h3 className="text-xl font-rajdhani font-bold text-white mb-4">
              Competitive Advantages
            </h3>
            <ul className="space-y-3 text-white/70">
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>First-mover advantage in hydrofoil technology</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Proprietary control systems and patents</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Strategic partnerships with industry leaders</span>
              </li>
              <li className="flex items-start">
                <div className="w-2 h-2 bg-primary rounded-full mt-2 mr-3 flex-shrink-0"></div>
                <span>Scalable manufacturing capabilities</span>
              </li>
            </ul>
          </div>
        </div>

        {/* Market Opportunity Highlight */}
        <div className="text-center">
          <div className="glass-panel inline-block px-8 py-6">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <TrendingUp className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-rajdhani font-bold text-white">
                  Massive Market Opportunity
                </h3>
                <p className="text-white/70">
                  $5.2B addressable market with 10.7% annual growth
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
} 