import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Award, TrendingUp, Globe, Zap } from 'lucide-react'

export default function AdvantagesSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const advantagesRef = useRef(null)

  const advantages = [
    {
      icon: Award,
      title: 'Market Leadership',
      description: 'First-mover advantage in hydrofoil technology',
      metric: '#1',
      color: 'from-yellow-400 to-orange-500'
    },
    {
      icon: TrendingUp,
      title: 'Growth Potential',
      description: '42B USD global maritime market opportunity',
      metric: '42B',
      color: 'from-green-400 to-emerald-500'
    },
    {
      icon: Globe,
      title: 'Global Reach',
      description: 'Operational capability across all major waterways',
      metric: '100%',
      color: 'from-blue-400 to-cyan-500'
    },
    {
      icon: Zap,
      title: 'Innovation Edge',
      description: 'Proprietary hydrofoil control systems',
      metric: '10+',
      color: 'from-purple-400 to-pink-500'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const advantages = advantagesRef.current

    // Initial setup
    gsap.set([title, advantages], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate advantages with stagger
        gsap.to(advantages, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.3,
          ease: 'power2.out'
        })
      }
    })

    return () => {
      ScrollTrigger.getAll().forEach(trigger => {
        if (trigger.vars.trigger === section) {
          trigger.kill()
        }
      })
    }
  }, [])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            Competitive <span className="cyan-gradient">Advantages</span>
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            Pravahan's unique position in the maritime technology landscape 
            with unmatched capabilities and market potential.
          </p>
        </div>

        {/* Advantages Grid */}
        <div 
          ref={advantagesRef}
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-16"
        >
          {advantages.map((advantage, index) => {
            const IconComponent = advantage.icon
            return (
              <div
                key={index}
                className="feature-card group cursor-pointer"
              >
                {/* Icon */}
                <div className={`w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br ${advantage.color} flex items-center justify-center group-hover:scale-110 transition-transform duration-300`}>
                  <IconComponent className="w-8 h-8 text-white" />
                </div>

                {/* Title */}
                <h3 className="text-xl font-rajdhani font-bold mb-4 text-white">
                  {advantage.title}
                </h3>

                {/* Metric */}
                <div className="text-3xl font-bold text-primary mb-2">
                  {advantage.metric}
                </div>

                {/* Description */}
                <p className="text-white/70 text-sm leading-relaxed">
                  {advantage.description}
                </p>
              </div>
            )
          })}
        </div>

        {/* Market Position Chart */}
        <div className="glass-panel p-8 mb-16">
          <h3 className="text-2xl font-rajdhani font-bold text-white mb-8 text-center">
            Market Position Analysis
          </h3>
          
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
            {/* Competitive Landscape */}
            <div>
              <h4 className="text-lg font-rajdhani font-bold text-white mb-4">
                Competitive Landscape
              </h4>
              <div className="space-y-4">
                <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <span className="text-white/70">Traditional Vessels</span>
                  <span className="text-white/50">80%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-primary/10 border border-primary/30 rounded-lg">
                  <span className="text-primary font-bold">Pravahan Hydrofoil</span>
                  <span className="text-primary font-bold">15%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <span className="text-white/70">Other Technologies</span>
                  <span className="text-white/50">5%</span>
                </div>
              </div>
            </div>

            {/* Growth Projection */}
            <div>
              <h4 className="text-lg font-rajdhani font-bold text-white mb-4">
                5-Year Growth Projection
              </h4>
              <div className="space-y-4">
                <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <span className="text-white/70">2024</span>
                  <span className="text-primary font-bold">15%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <span className="text-white/70">2025</span>
                  <span className="text-primary font-bold">25%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-white/5 rounded-lg">
                  <span className="text-white/70">2026</span>
                  <span className="text-primary font-bold">35%</span>
                </div>
                <div className="flex items-center justify-between p-3 bg-primary/10 border border-primary/30 rounded-lg">
                  <span className="text-primary font-bold">2029</span>
                  <span className="text-primary font-bold">50%</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Key Differentiators */}
        <div className="text-center">
          <div className="glass-panel inline-block px-8 py-6">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <Award className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-rajdhani font-bold text-white">
                  Unmatched Innovation
                </h3>
                <p className="text-white/70">
                  Proprietary technology with 10+ patents and growing
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
} 