import React, { useEffect, useRef, useState } from 'react'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import { Zap, Target, Gauge, Users } from 'lucide-react'

export default function SolutionSection() {
  const sectionRef = useRef(null)
  const titleRef = useRef(null)
  const specsRef = useRef(null)
  const [activeSpec, setActiveSpec] = useState(0)

  const specifications = [
    {
      icon: Zap,
      title: 'Speed',
      value: '3X',
      unit: 'faster',
      description: 'than conventional vessels',
      detail: 'Achieving speeds up to 60 knots through advanced hydrofoil technology',
      color: 'from-yellow-400 to-orange-500'
    },
    {
      icon: Target,
      title: 'Range',
      value: '500',
      unit: 'nm',
      description: 'operational range',
      detail: 'Extended range capability for long-distance maritime operations',
      color: 'from-green-400 to-emerald-500'
    },
    {
      icon: Gauge,
      title: 'Efficiency',
      value: '70%',
      unit: 'less fuel',
      description: 'consumption',
      detail: 'Significant reduction in fuel consumption and operational costs',
      color: 'from-blue-400 to-cyan-500'
    },
    {
      icon: Users,
      title: 'Capacity',
      value: '150',
      unit: 'passengers',
      description: 'or 50 tons cargo',
      detail: 'Versatile payload capacity for both passenger and cargo operations',
      color: 'from-purple-400 to-pink-500'
    }
  ]

  useEffect(() => {
    const section = sectionRef.current
    const title = titleRef.current
    const specs = specsRef.current

    // Initial setup
    gsap.set([title, specs], { opacity: 0, y: 100 })

    // Scroll-triggered animations
    ScrollTrigger.create({
      trigger: section,
      start: 'top 80%',
      end: 'bottom 20%',
      onEnter: () => {
        // Animate title
        gsap.to(title, {
          opacity: 1,
          y: 0,
          duration: 1,
          ease: 'power2.out'
        })

        // Animate specs with stagger
        gsap.to(specs, {
          opacity: 1,
          y: 0,
          duration: 0.8,
          delay: 0.3,
          ease: 'power2.out'
        })
      }
    })

    // Auto-rotate through specs
    const interval = setInterval(() => {
      setActiveSpec((prev) => (prev + 1) % specifications.length)
    }, 3000)

    return () => {
      clearInterval(interval)
    }
  }, [specifications.length])

  // Animate counter when spec becomes active
  useEffect(() => {
    const specElement = document.querySelector(`[data-spec="${activeSpec}"]`)
    if (specElement) {
      const valueElement = specElement.querySelector('.spec-value')
      const spec = specifications[activeSpec]
      
      gsap.fromTo(valueElement, 
        { scale: 0.8, opacity: 0.5 },
        { 
          scale: 1, 
          opacity: 1, 
          duration: 0.5, 
          ease: 'back.out(1.7)' 
        }
      )
    }
  }, [activeSpec, specifications])

  return (
    <section 
      ref={sectionRef}
      className="scroll-section py-20 px-4"
    >
      <div className="max-w-7xl mx-auto">
        {/* Section Title */}
        <div className="text-center mb-16">
          <h2 
            ref={titleRef}
            className="section-title"
          >
            The <span className="cyan-gradient">Solution</span>
          </h2>
          <p className="text-xl text-white/70 max-w-3xl mx-auto">
            Revolutionary hydrofoil technology that transforms maritime transportation 
            with unprecedented speed, efficiency, and operational capabilities.
          </p>
        </div>

        {/* Specifications Dashboard */}
        <div 
          ref={specsRef}
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-16"
        >
          {specifications.map((spec, index) => {
            const IconComponent = spec.icon
            const isActive = index === activeSpec
            
            return (
              <div
                key={index}
                data-spec={index}
                className={`feature-card group cursor-pointer transition-all duration-500 ${
                  isActive ? 'scale-105 shadow-[0_0_60px_rgba(0,255,255,0.3)]' : ''
                }`}
                onClick={() => setActiveSpec(index)}
              >
                {/* Icon */}
                <div className={`w-16 h-16 mx-auto mb-6 rounded-full bg-gradient-to-br ${spec.color} flex items-center justify-center group-hover:scale-110 transition-transform duration-300`}>
                  <IconComponent className="w-8 h-8 text-white" />
                </div>

                {/* Title */}
                <h3 className="text-xl font-rajdhani font-bold mb-2 text-white">
                  {spec.title}
                </h3>

                {/* Value */}
                <div className="spec-value text-4xl font-bold text-primary mb-2">
                  {spec.value}
                  <span className="text-2xl text-white/70 ml-1">{spec.unit}</span>
                </div>

                {/* Description */}
                <p className="text-white/70 text-sm mb-4">
                  {spec.description}
                </p>

                {/* Active indicator */}
                {isActive && (
                  <div className="absolute top-4 right-4 w-3 h-3 bg-primary rounded-full animate-pulse"></div>
                )}
              </div>
            )
          })}
        </div>

        {/* Active Spec Detail */}
        <div className="glass-panel p-8 text-center">
          <div className="max-w-2xl mx-auto">
            <h3 className="text-2xl font-rajdhani font-bold text-white mb-4">
              {specifications[activeSpec].title} Advantage
            </h3>
            <p className="text-lg text-white/80 leading-relaxed">
              {specifications[activeSpec].detail}
            </p>
          </div>
        </div>

        {/* Technology Highlight */}
        <div className="mt-16 text-center">
          <div className="glass-panel inline-block px-8 py-6">
            <div className="flex items-center justify-center space-x-4">
              <div className="w-12 h-12 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center">
                <Zap className="w-6 h-6 text-white" />
              </div>
              <div>
                <h3 className="text-xl font-rajdhani font-bold text-white">
                  Hydrofoil Technology
                </h3>
                <p className="text-white/70">
                  Lifting the vessel above water for maximum efficiency
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  )
} 